
*{
  margin:0px;
}

.app{
  background-color:#111;
}



/*----------- row css ----------*/
.row{
  margin-left: 20px;
  color: white;
}
.row_poster{
  width: 100%;
  object-fit: contain;
  max-height: 100px;
  transition: transform 450ms;
  margin-right: 10px;
  padding:20px;
}

.row_posters::-webkit-scrollbar{
  width: 5px;
  
}

.row_poster:hover{
transform: scale(1.08);
}

.row_posters{
  display: flex;
  flex-direction: row;
  overflow-y: hidden;
  overflow-x: scroll;
}

.row_posterLarge:hover{
  transform: scale(1.09);
  opacity: 1;
  }
.row_posterLarge{
  max-height: 250px;
}
/*----------- row css ----------*/






/*----------- banner css ------------*/
.banner{
  color: white;
  object-fit: contain;
  height: 448px;
}


.banner_contents{
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner_title{
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner_description{
  width: 45rem;
  line-height: 1.3;
  padding-top:1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}

.banner_button{
  cursor: pointer;
  color: #fff;
  outline: none;
  border:none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51,51,51,0.5);
  padding-bottom: 0.5rem;
}

.banner_button:hover{
color: black;
background-color: white;
transition: all 0.2s;

}




.banner-fadeBottom{
  height: 7.4rem;
  background-image:linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);

}
/*----------- banner css ------------*/





/*----------- nav css ------------*/
.nav{
  position: fixed;
  top:0px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 20px;
  
  height: 30px;
  z-index: 1;



  /*animation*/

transition-timing-function: ease-in;
transition: all 0.5s;

}

.nav_black{
  background-color: black;
}
.nav_logo{
  position: fixed;
  left: 20px;
width:100px;
object-fit: contain;
}


.nav_avatar{
position: fixed;
right: 20px;
  width: 30px;
  object-fit: contain;

}


/*----------- nav css ------------*/




/*----------- footer css ------------*/

.footer{
  border-top:8px solid rgb(43, 42, 42);
  width: 100%;
  display: flex;
}

.footer_div{
 flex-direction: column;
  display: flex;
  margin: 120px;
  line-height: 40px;
}

.footer_div div{
  color:gray;
}

/*----------- footer css ------------*/